.dashboard {
  width: calc(100% - 280px);
  margin-left: 280px;
  height:100vh;
  max-height: 100vh;
  overflow-x:hidden;
  position: relative;
  .content{
    padding: 1px 20px;
  }


}

.row1 {
  color: rgba(255, 255, 255, 0.6) !important;
  font-weight: 300;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  & > div {
    width: calc(50% - 10px);
  }
}

.row2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  & > div {
    width: calc(33% - 13px);
  }

  label {
    color: rgba(255, 255, 255, 0.86);
  }
}

@media screen and (max-width: 1024px) {
  .dashboard {
    width: calc(100% - 95px);
    margin-left: 95px;
  }

}

.filterPanel {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items:center;
  margin-bottom: 20px;
  position: relative;

  & > div {
    margin-right: 20px;
  }
  .MuiFilledInput-root {
    height: 56px;
  }
  // #393d48
  .filterPanel__select {
    background: #3f434d;
    border-radius: 4px 4px 0px 0px;
    overflow:hidden;
    margin-right: 2px;
    height: 56px;
    min-width: 210px;
    .MuiFormControl-root label {
      font-size: 13px;
    }
  }

  .filterPanel__add {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items:center;
    width: 56px;
    min-width:unset !important;
    box-sizing:border-box;
    height: 56px;
    border-radius:50%;
    background: #A1AFDB;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2), 0px 1px 10px rgba(0, 0, 0, 0.12), 0px 4px 5px rgba(0, 0, 0, 0.14);
    cursor:pointer;
    margin-right: 0;
  }
  .filterPanel__edit {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items:center;
    min-width: 40px;
    height: 40px;
    border-radius:50%;
    background: #414552;
    cursor:pointer;
    margin-right: 12px;
  }
  .filterPanel__select__order {
    min-width: 56px;
    background: #434753;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items:center;
    border-radius: 4px 4px 0px 0px;
    height: 56px;
    border-bottom: 1px solid rgba(255,255,255,0.67);
    cursor:pointer;
    transition-duration:.3s;
    &:hover {
      background-color: #3e4350;
      transition-duration:.3s;
    }
    &--active {
      background: #363b45;
      &:hover {
        background: #343842;
      }
    }

  }
  &__doubled {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items:center;
    & > div {
      margin-right: 2px;
    }
  }
  .w160 {
    width: 160px;
  }
  .w120 {
    width: 120px;
  }
  .w100percent {
    width: 100%;
  }
  .MuiTextField-root {
    margin-right: 0;
  }
  .MuiSelect-root {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items:center;
    padding-left: 14px;
    font-size: 15px;
    padding-top: 21px;
    letter-spacing: 0.1px;
    svg {
      margin-right: 10px;
      margin-top: -17px;
    }
  }
  .custom_label {
    .MuiInputLabel-filled.MuiInputLabel-shrink {
      transform: translate(48px, 10px) scale(0.75);
      font-size: 16px;
      margin-left: 36px;
    }
    svg {
      width: 24px;
    }
  }
  label {
    font-size: 16px;
  }
  .search_area {
    border-radius: 4px 4px 0px 0px;
    overflow:hidden;
    input {
      padding: 27px 12px 10px;
      height: 56px;
      font-weight: 300;
      box-sizing: border-box;
      padding-top: 10px;
      padding-left: 52px;
      font-size: 15px;
      background: #434753 url('../../Images/search.svg') 15px 15px no-repeat;
      &::placeholder {
        font-size: 15px;
        letter-spacing:0.5px;
      }
    }
  }
  .search_additional_components {
    position: absolute;
    right: 3px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items:center;
    margin-right: 0;
    div {
      margin-right: 13px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items:center;
    }
  }
}